
/* 🚀 MODERN STUDENT TERMS DESIGN */
:root {
  --primary-color: #1e40af;
  --primary-dark: #1e3a8a;
  --bg-soft: #f8fafc;
  --text-main: #0f172a;
  --text-soft: #64748b;
  --border-color: #e2e8f0;
  --shadow-sm: 0 4px 6px -1px rgb(0 0 0 / 0.1);
  --shadow-md: 0 10px 15px -3px rgb(0 0 0 / 0.1);
}

.student-terms-supreme {
  min-height: 100vh;
  background: var(--bg-soft);
  padding-bottom: 50px;
  direction: rtl;
}

/* ─────────────────────────────────────────────────────────
   💎 HEADER
   ───────────────────────────────────────────────────────── */
.header-supreme {
  background: white;
  padding: 0;
  box-shadow: var(--shadow-sm);
  position: sticky;
  top: 0;
  z-index: 1000;
  border-bottom: 1px solid var(--border-color);
}

.header-content-supreme {
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 30px;
}

.logo-section h1 {
  font-size: 1.6rem;
  font-weight: 900;
  color: var(--primary-color);
  margin: 0;
  letter-spacing: -0.5px;
}

.header-right-section {
  display: flex;
  align-items: center;
  gap: 15px;
}

.user-profile-supreme {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 6px 15px;
  background: #f1f5f9;
  border-radius: 12px;
  border: 1px solid var(--border-color);
}

.user-avatar-supreme {
  width: 38px;
  height: 38px;
  background: var(--primary-color);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  color: white;
  font-size: 1.1rem;
}

.user-info-supreme {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.welcome-label {
  font-size: 0.65rem;
  color: var(--text-soft);
  font-weight: 600;
}

.user-name-supreme {
  font-size: 0.85rem;
  font-weight: 700;
  color: var(--text-main);
  margin: 0;
}

.header-actions-supreme {
  display: flex;
  align-items: center;
  gap: 10px;
}

.btn-logout-supreme {
  background: #fee2e2;
  color: #ef4444;
  border: none;
  padding: 10px 18px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 700;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-logout-supreme:hover {
  background: #fecaca;
  transform: translateY(-2px);
}

/* ─────────────────────────────────────────────────────────
   🎯 MAIN CONTENT
   ───────────────────────────────────────────────────────── */
.terms-main-supreme {
  max-width: 1200px;
  margin: 60px auto;
  padding: 0 30px;
}

.terms-hero-supreme {
  text-align: center;
  margin-bottom: 50px;
}

.terms-hero-supreme h2 {
  font-size: 2.5rem;
  font-weight: 900;
  color: var(--text-main);
  margin-bottom: 12px;
}

.terms-hero-supreme p {
  color: var(--text-soft);
  font-size: 1.1rem;
  font-weight: 500;
}

.terms-grid-supreme {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 30px;
}

.term-card-supreme {
  background: white;
  border-radius: 24px;
  padding: 40px;
  text-align: center;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid var(--border-color);
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.05);
  position: relative;
  overflow: hidden;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 25px;
}

.term-card-supreme::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 6px;
  background: var(--primary-color);
}

.term-card-supreme:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 25px -5px rgb(0 0 0 / 0.1);
  border-color: var(--primary-color);
}

.term-icon-supreme {
  width: 90px;
  height: 90px;
  background: #eff6ff;
  border-radius: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-color);
  font-size: 2.8rem;
  transition: all 0.3s ease;
}

.term-card-supreme:hover .term-icon-supreme {
  background: var(--primary-color);
  color: white;
  transform: scale(1.1);
}

.term-title-supreme {
  font-size: 1.6rem;
  font-weight: 800;
  color: var(--text-main);
  margin: 0;
}

.term-status-supreme {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 12px;
  border-radius: 100px;
  font-size: 0.8rem;
  font-weight: 700;
  border: 1px solid var(--border-color);
  background: #f1f5f9;
  color: var(--text-soft);
}

.term-status-supreme.status-active {
  background: #eff6ff;
  color: var(--primary-color);
  border-color: #bfdbfe;
}

.term-btn-supreme {
  margin-top: auto;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  background: white;
  border: 1px solid var(--border-color);
  border-radius: 12px;
  color: var(--text-main);
  font-weight: 700;
  font-size: 0.9rem;
  transition: all 0.2s ease;
}

.term-card-supreme:hover .term-btn-supreme {
  background: var(--primary-color);
  color: white;
  border-color: var(--primary-color);
  transform: translateY(-2px);
}

/* ─────────────────────────────────────────────────────────
   📱 RESPONSIVE
   ───────────────────────────────────────────────────────── */
@media (max-width: 768px) {
  .header-content-supreme {
    padding: 12px 20px;
  }
  
  .terms-main-supreme {
    margin: 40px auto;
  }
  
  .terms-hero-supreme h2 {
    font-size: 2rem;
  }
  
  .terms-grid-supreme {
    grid-template-columns: 1fr;
  }
}
