
/* 🚀 MODERN STUDENT SUBJECT DETAIL DESIGN */
:root {
  --primary-color: #1e40af;
  --primary-dark: #1e3a8a;
  --bg-soft: #f8fafc;
  --text-main: #0f172a;
  --text-soft: #64748b;
  --border-color: #e2e8f0;
  --shadow-sm: 0 4px 6px -1px rgb(0 0 0 / 0.1);
  --shadow-md: 0 10px 15px -3px rgb(0 0 0 / 0.1);
}

.student-detail-supreme {
  min-height: 100vh;
  background: var(--bg-soft);
  direction: rtl;
  padding-bottom: 50px;
}

/* ─────────────────────────────────────────────────────────
   💎 HEADER
   ───────────────────────────────────────────────────────── */
.header-supreme {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: white;
  border-bottom: 1px solid var(--border-color);
  box-shadow: var(--shadow-sm);
}

.header-content-supreme {
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 30px;
}

.logo-section h1 {
  font-size: 1.6rem;
  font-weight: 900;
  color: var(--primary-color);
  margin: 0;
  cursor: pointer;
}

.header-right-section {
  display: flex;
  align-items: center;
  gap: 20px;
}

.user-profile-supreme {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 6px 15px;
  background: #f1f5f9;
  border-radius: 12px;
  border: 1px solid var(--border-color);
}

.user-avatar-supreme {
  width: 38px;
  height: 38px;
  background: var(--primary-color);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 800;
  color: white;
  font-size: 1.1rem;
}

.user-info-supreme {
  display: flex;
  flex-direction: column;
}

.welcome-label {
  font-size: 0.7rem;
  color: var(--text-soft);
  font-weight: 600;
}

.user-name-supreme {
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--text-main);
  margin: 0;
}

.btn-logout-supreme {
  background: #fee2e2;
  color: #ef4444;
  border: none;
  padding: 10px 18px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 700;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-logout-supreme:hover {
  background: #fecaca;
  transform: translateY(-2px);
}

/* ─────────────────────────────────────────────────────────
   🎭 HERO & HEADER
   ───────────────────────────────────────────────────────── */
.detail-main-supreme {
  max-width: 1400px;
  margin: 90px auto 0;
  padding: 0 30px;
}

.subject-header-supreme {
  background: white;
  border-radius: 30px;
  padding: 40px;
  margin-bottom: 30px;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
}

.header-nav-supreme {
  margin-bottom: 25px;
}

.btn-back-supreme {
  display: flex;
  align-items: center;
  gap: 8px;
  background: #f8fafc;
  color: var(--text-main);
  border: 1px solid var(--border-color);
  padding: 8px 20px;
  border-radius: 100px;
  font-weight: 700;
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-back-supreme:hover {
  background: white;
  color: var(--primary-color);
  border-color: var(--primary-color);
  transform: translateX(5px);
}

.subject-hero-section {
  display: flex;
  gap: 40px;
  align-items: center;
}

.subject-image-container {
  width: 200px;
  height: 200px;
  border-radius: 25px;
  overflow: hidden;
  box-shadow: var(--shadow-md);
  border: 4px solid white;
}

.hero-img-supreme {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero-placeholder-supreme {
  width: 100%;
  height: 100%;
  background: #eff6ff;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-color);
}

.subject-text-content h1 {
  font-size: 2.8rem;
  font-weight: 900;
  color: var(--text-main);
  margin-bottom: 15px;
}

.subject-desc-supreme {
  color: var(--text-soft);
  font-size: 1.1rem;
  line-height: 1.6;
  margin-bottom: 25px;
  max-width: 800px;
}

/* RATING */
.rating-interactive-supreme {
  display: flex;
  align-items: center;
  gap: 15px;
  background: #fffbeb;
  padding: 12px 20px;
  border-radius: 15px;
  width: fit-content;
  border: 1px solid #fde68a;
}

.rating-label {
  font-weight: 800;
  color: #92400e;
  font-size: 0.95rem;
}

.stars-interactive {
  display: flex;
  gap: 5px;
}

.star-icon {
  font-size: 1.4rem;
  color: #d1d5db;
  cursor: pointer;
  transition: all 0.2s;
}

.star-icon:hover {
  transform: scale(1.2);
}

.star-icon.active {
  color: #fbbf24;
  fill: #fbbf24;
}

/* ─────────────────────────────────────────────────────────
   📑 TABS
   ───────────────────────────────────────────────────────── */
.tabs-container-supreme {
  position: sticky;
  top: 80px;
  z-index: 100;
  background: var(--bg-soft);
  padding: 15px 0;
  margin-bottom: 30px;
}

.tabs-supreme {
  display: flex;
  gap: 15px;
  background: white;
  padding: 10px;
  border-radius: 20px;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
  width: fit-content;
}

.tab-btn-supreme {
  padding: 12px 25px;
  border-radius: 15px;
  border: none;
  background: transparent;
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 800;
  color: var(--text-soft);
  cursor: pointer;
  transition: all 0.3s;
}

.tab-btn-supreme.active {
  background: var(--primary-color);
  color: white;
  box-shadow: 0 4px 12px rgba(30, 64, 175, 0.3);
}

.count-badge {
  background: #f1f5f9;
  color: var(--text-soft);
  padding: 2px 8px;
  border-radius: 8px;
  font-size: 0.8rem;
}

.tab-btn-supreme.active .count-badge {
  background: rgba(255,255,255,0.2);
  color: white;
}

/* ─────────────────────────────────────────────────────────
   📦 PDFS GRID
   ───────────────────────────────────────────────────────── */
.pdfs-grid-supreme {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  gap: 25px;
}

.pdf-card-supreme {
  background: white;
  border-radius: 25px;
  padding: 30px;
  border: 1px solid var(--border-color);
  transition: all 0.3s;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.pdf-card-supreme:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-md);
  border-color: var(--primary-color);
}

.pdf-icon-box {
  width: 55px;
  height: 55px;
  background: #eff6ff;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-color);
}

.pdf-details-supreme h4 {
  font-size: 1.25rem;
  font-weight: 800;
  color: var(--text-main);
  margin-bottom: 10px;
}

.pdf-meta-supreme {
  display: flex;
  gap: 20px;
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--text-soft);
}

.pdf-meta-supreme .meta-item {
  display: flex;
  align-items: center;
  gap: 6px;
}

.pdf-meta-supreme .cost {
  color: #059669;
  background: #ecfdf5;
  padding: 2px 10px;
  border-radius: 100px;
}

.btn-download-supreme {
  width: 100%;
  padding: 15px;
  border-radius: 15px;
  border: none;
  background: var(--primary-color);
  color: white;
  font-weight: 800;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-download-supreme:hover {
  background: var(--primary-dark);
  transform: scale(1.02);
}

/* ─────────────────────────────────────────────────────────
   🖼️ IMAGES GRID
   ───────────────────────────────────────────────────────── */
.images-grid-supreme {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 25px;
}

.image-card-supreme {
  background: white;
  border-radius: 25px;
  overflow: hidden;
  border: 1px solid var(--border-color);
  cursor: pointer;
  transition: all 0.4s;
}

.image-card-supreme:hover {
  transform: scale(1.03);
  box-shadow: var(--shadow-md);
}

.image-wrapper-supreme {
  position: relative;
  height: 250px;
}

.image-wrapper-supreme img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-overlay-supreme {
  position: absolute;
  inset: 0;
  background: linear-gradient(to top, rgba(0,0,0,0.8), transparent);
  opacity: 0;
  transition: 0.3s;
  display: flex;
  align-items: flex-end;
  padding: 20px;
}

.image-card-supreme:hover .image-overlay-supreme {
  opacity: 1;
}

.overlay-content h4 {
  color: white;
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: 5px;
}

.overlay-content .views-count {
  color: rgba(255,255,255,0.7);
  font-size: 0.85rem;
}

/* ─────────────────────────────────────────────────────────
   🏆 EXAMS GRID
   ───────────────────────────────────────────────────────── */
.exams-grid-supreme {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 25px;
}

.exam-card-supreme {
  background: white;
  border-radius: 25px;
  padding: 30px;
  border: 1px solid var(--border-color);
  position: relative;
  transition: all 0.3s;
}

.exam-card-supreme:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-md);
}

.exam-card-supreme.locked {
  opacity: 0.7;
  background: #f1f5f9;
}

.status-badge {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 6px 15px;
  border-radius: 100px;
  font-weight: 800;
  font-size: 0.85rem;
  width: fit-content;
  margin-bottom: 20px;
}

.status-badge.passed { background: #dcfce7; color: #166534; }
.status-badge.locked { background: #fee2e2; color: #991b1b; }
.status-badge.available { background: #eff6ff; color: #1e40af; }

.exam-title-supreme {
  font-size: 1.4rem;
  font-weight: 900;
  color: var(--text-main);
  margin-bottom: 20px;
}

.exam-info-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
  margin-bottom: 25px;
}

.info-pill {
  background: #f8fafc;
  padding: 10px;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.info-pill.highlight {
  background: #fffbeb;
  border: 1px solid #fde68a;
}

.info-pill .label { font-size: 0.75rem; color: var(--text-soft); font-weight: 700; }
.info-pill .value { font-size: 1rem; color: var(--text-main); font-weight: 800; }

.btn-start-exam-supreme {
  width: 100%;
  padding: 14px;
  border-radius: 15px;
  border: none;
  background: var(--primary-color);
  color: white;
  font-weight: 800;
  cursor: pointer;
  transition: 0.3s;
}

.unlock-hint {
  text-align: center;
  color: #ef4444;
  font-size: 0.85rem;
  font-weight: 700;
}

/* ─────────────────────────────────────────────────────────
   📱 RESPONSIVE
   ───────────────────────────────────────────────────────── */
@media (max-width: 992px) {
  .subject-hero-section { flex-direction: column; text-align: center; }
  .subject-image-container { margin: 0 auto; }
  .rating-interactive-supreme { margin: 0 auto; }
  .tabs-supreme { width: 100%; overflow-x: auto; }
}

@media (max-width: 768px) {
  .detail-main-supreme { padding: 0 15px; margin-top: 80px; }
  .subject-header-supreme { padding: 25px; }
  .subject-text-content h1 { font-size: 2rem; }
  .pdfs-grid-supreme, .exams-grid-supreme { grid-template-columns: 1fr; }
}
